import { round } from './Math';

export const toLocaleCurrency = (amount, percision = 2) => {
  console.log(amount);
  return round(amount).toLocaleString('en-EN', {
      minimumFractionDigits: percision,
      maximumFractionDigits: percision,
  })
};



